body {
  margin: 0;
  background-color: #111;
  color: #ccc;
  font-size: 1.2em; }

.fade-up {
  position: relative;
  -webkit-animation: fade-up 1s ease;
          animation: fade-up 1s ease; }

@-webkit-keyframes fade-up {
  from {
    bottom: -1.2em;
    opacity: 0; }
  to {
    bottom: 0em;
    opacity: 1; } }

@keyframes fade-up {
  from {
    bottom: -1.2em;
    opacity: 0; }
  to {
    bottom: 0em;
    opacity: 1; } }



